.f {
  height: 50px;
  background-color: rgb(0, 44, 27);
  color: aliceblue;
  margin-top: 10px;
  display: flex;
}

.f-left {
  flex: 1;
}

.f-right {
  flex: 1;
}

.f-copy {
  margin-left: 10px;
  display: flex;
  align-items: center;
  height: 100%;
}

.f-social {
  display: flex;
  align-items: center;
  height: 100%;
  justify-content: end;
  margin-right: 10px;
}

.f-icon {
  width: 25px !important;
  height: 25px;
  margin-right: 5px;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: aliceblue;
}
