.pl {
  padding: 50px 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.pl-texts {
  width: 65%;
}

.pl-title {
  font-size: 50px;
  font-weight: 600;
}

.pl-desc {
  margin: 20px 0px;
}

.pl-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.p {
  width: 30%;
  height: 40vh;
  margin: 20px 10px;
  border: 2px solid rgb(192, 192, 192);
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
}

.p-browser {
  height: 20px;
  background-color: rgb(192, 192, 192);
  display: flex;
  align-items: center;
  position: sticky;
  z-index: 2;
}

.p-circle {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  margin: 3px;
  background-color: white;
}

.p-img {
  width: 100%;
  transition: all 10s ease;
}

.p:hover .p-img {
  transform: translateY(-100%);
}

@media screen and (max-width: 820px) {
  .pl {
    padding: 50px 10px 10px 10px;
  }

  .pl-texts {
    width: 100%;
  }

  .p {
    width: 40%;
    height: 20vh;
  }
}

@media screen and (max-width: 600px) {
  .pl {
    padding: 15px 10px 10px 10px;
  }

  .pl-texts {
    width: 100%;
  }

  .p {
    width: 40%;
    height: 20vh;
  }
}
